import React, { Component } from 'react';
import { Route, Link } from "react-router-dom";
import "./Main.css";
import Logo from "../../images/logo.png";
import Search from "../../images/search.svg";
import Home from "../Home/Home";
import Formacion from '../Formacion/Formacion';
import Beneficios from '../Beneficios/Beneficios'
import Blog from "../Blog/Blog";
import Asociacion from "../Asociacion/Asociacion";
import Contacto from "../Contacto/Contacto";
import MediaQuery from "react-responsive";
import BurgerIcon from "../../components/BurgerIcon/BurgerIcon";
const Main = () => (
  <div className="container">
    <MediaQuery query="(max-width:1059px)">
      <nav className="navbar-mobile">
        <div>
          <BurgerIcon />
            

          <Link to="/">
            <img src={Logo} alt="Addpol Home" className="logo" />
          </Link>
        </div>
      </nav>
      <div className="main-content">
          <Route exact path="/" component={Home} />
          <Route path="/formacion" component={Formacion} />
          <Route path="/beneficios" component={Beneficios} />
          <Route path="/blog" component={Blog} />
          <Route path="/asociacion" component={Asociacion} />
          <Route path="/contacto" component={Contacto} />
      </div>
    </MediaQuery>
    <MediaQuery query="(min-width: 1060px)">
      <nav className="navbar">
          <div className="logo">
          <Link to="/">
            <img src={Logo} alt="Addpol Home" className="logo" />
          </Link>
        </div>
        <div className="nav-inside">
          <ul className="nav-left">
            <li>
              <Link to="/formacion">formación</Link>
            </li>
            <li>
              <Link to="/beneficios">beneficios</Link>
            </li>
            <li>
              <Link to="/blog">blog</Link>
            </li>
            <li>
              <Link to="asociacion">la asociación</Link>
            </li>
            <li>
              <Link to="contacto">contacto</Link>
            </li>
          </ul>

          <ul className="nav-right">
            <li>
              <Link to="/search">
                <img src={Search} alt="Addpol Buscar" style={{width: 30}}/>
              </Link>
            </li>
            <li>
                              <Link to="/signin"><button className="boton" type="submit">Acceso al Campus Virtual</button></Link>
            </li>
          </ul>
        </div>
      </nav>
      <div>
          <Route exact path="/" component={Home} />
          <Route path="/formacion" component={Formacion} />
          <Route path="/beneficios" component={Beneficios} />
          <Route path="/blog" component={Blog} />
          <Route path="/asociacion" component={Asociacion} />
          <Route path="/contacto" component={Contacto} />
      </div>
    </MediaQuery>

    
  </div>
);





export default Main;



