import React from 'react'
import formacion1x from "../../images/formacion@1x.jpg";
import formacion2x from "../../images/formacion@2x.jpg";
import formacion3x from "../../images/formacion@3x.jpg";
export default () => {
  return (
    <div className="container">
      <picture>
              <source media="(min-width: 1024px)" srcSet={formacion3x} />
              <source media="(min-width: 728px)" srcSet={formacion2x} />
            <img src={formacion1x} alt="" />
      </picture>
    </div>
  )
}



