
import React, { Component } from 'react';
import { slide as Menu } from "react-burger-menu";
import './BurgerIcon.scss';
import { Link } from "react-router-dom";
import Search from "../../images/search.svg";

export default class BurgerIcon extends Component {
    
  render() {
     var styles = { bmBurgerButton: { position: "fixed", width: "36px", height: "30px", left: "36px", top: "36px" }, bmBurgerBars: { background: "#373a47" }, bmCrossButton: { height: "24px", width: "24px" }, bmCross: { background: "#bdc3c7" }, bmMenu: { background: "#e6e7e2", padding: "2.5em 1.5em 0", fontSize: "1.15em", marginLeft: "-10px" }, bmMorphShape: { fill: "#373a47" }, bmItemList: { color: "#b8b7ad", padding: "0.8em" }, bmOverlay: { background: "rgba(0, 0, 0, 0.3)" } };
    return <div>
        <Menu styles={styles}>
          <ul>
            <li>
              <Link to="/formacion" className="menu-item">
                formación
              </Link>
            </li>
            <li>
              <Link to="/beneficios" className="menu-item">
                beneficios
              </Link>
            </li>
            <li>
              <Link to="/blog" className="menu-item">
                blog
              </Link>
            </li>
            <li>
              <Link to="asociacion" className="menu-item">
                la asociación
              </Link>
            </li>
            <li>
              <Link to="contacto" className="menu-item">
                contacto
              </Link>
            </li>
          </ul>

          <ul>
            <li>
              <Link to="/search" className="menu-item">
                <img src={Search} alt="Addpol Buscar" style={{width: 40}}/>
              </Link>
            </li>
            <li>
              <Link to="/signin" className="menu-item">
              <button className="boton" type="submit">Acceso al Campus Virtual</button>
              </Link>
            </li>
          </ul>
        </Menu>
      </div>;
  }
}



