import React from 'react'
import asociacion1x from "../../images/asociacion@1x.jpg";
import asociacion2x from "../../images/asociacion@2x.jpg";
import asociacion3x from "../../images/asociacion@3x.jpg";
export default () => {
    return (
        <div className="container">
            <picture>
                <source media="(min-width: 1024px)" srcSet={asociacion3x} />
                <source media="(min-width: 728px)" srcSet={asociacion2x} />
                <img src={asociacion1x} alt="" />
            </picture>
        </div>
    )
}



