import React from 'react'
import beneficios1x from "../../images/beneficios@1x.jpg";
import beneficios2x from "../../images/beneficios@2x.jpg";
import beneficios3x from "../../images/beneficios@3x.jpg";
export default () => {
    return (
        <div className="container">
            <picture>
                <source media="(min-width: 1024px)" srcSet={beneficios3x} />
                <source media="(min-width: 728px)" srcSet={beneficios2x} />
                <img src={beneficios1x} alt="" />
            </picture>
        </div>
    )
}



